@use 'base/variables' as v;
@use 'base/mixins' as m;

.boton {           
    @include m.boton2(v.$azul, v.$blanco);
    border-radius: .5rem; 
}

.botons {
    @include m.boton3(v.$azul, v.$blanco);
    border-radius: .5rem;
}

.boton-eliminar {
    @include m.boton3(v.$rojo, v.$blanco);
    border-radius: .5rem;
}