@use 'base/variables' as v;
@use 'base/mixins' as m;

.citas {

    list-style: none;
    padding: 0;

    li {

        @include m.telefono {
                text-align: left;
            }
            
        padding:1rem;
        border-bottom: 1px solid v.$blanco;
        text-align: center;

        p {
            font-weight: v.$bold;
            color: v.$azul;
        }

        .servicio, span {
            font-weight: v.$regular;
            color: v.$blanco;
        }
    }
}

.servicios {
    @extend.citas;
    margin: 0;
    // .botons {
    //     @include m.tablet {
    //         width: auto;
    //     }
    // }
    .boton-eliminar {
        width: 100%;
    }
}