@use 'base/variables' as v;
@use 'base/mixins' as m;

.listado-servicios {

    display: grid;
    gap: 2rem;

    @include m.tablet {
        @include m.grid(2, 2rem)
    }

    .servicio {

        background-color: v.$blanco;
        border-radius: 1rem;
        padding: 2rem;
        transition-property: transform;
        transition-duration: 300ms;
        text-align: center;

        &.seleccionado {
            background-color: v.$azul;
            
            p {
                color: v.$blanco;
            }
        }

        &:hover {
            transform: scale(1.1);
        }

        p {
            color: v.$negro;
            margin: 0 0 cacl(v.$separacion / 4) 0;
        }

        .precio-servicio {
            color: v.$azul;
            font-weight: bold;
            font-size: 2.5rem;
            margin: 0;
        }

    }

}